* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

#totalMiles {
	font-family: 'Viga', sans-serif;
	font-size: 7rem;
	color: white;
	text-shadow: 3px 3px #00695c;
	/*border: 1pt black solid;*/
	/*border-radius: 50%;*/
	margin: 0px auto;
	padding-top: 25%;
	letter-spacing: -2px;
}

#milesLeft {
	font-size: 4rem;
	font-family: 'Viga', sans-serif;
	color: red;
	margin:0px auto;
}

.img-circle {
	border: 1pt #558b2f solid;
	width: 200px;
	height: 200px;
	margin: 0px auto;
	/*background-color: #eeeeee;*/
	background: -webkit-gradient(linear, left top, right bottom, from(#9ccc65), to(#33691e));
	background: -webkit-linear-gradient(top left, #9ccc65, #33691e);
	background: -o-linear-gradient(top left, #9ccc65, #33691e);
	background: linear-gradient(to bottom right, #9ccc65, #33691e);
	-webkit-box-shadow: 3px 3px 7px #9e9e9e;
	        box-shadow: 3px 3px 7px #9e9e9e;
}

